* {
  border: 0;
  margin: 0;
  padding: 0;
}

:focus, :active, a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  line-height: 1;
}

html {
  width: 100vw;
  overflow-x: hidden;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a:visited {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  width: 100%;
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

.cub_wrapper {
  perspective: 500px;
  margin-right: 10px;
}

.cub_wrapper:hover > .cube > .side, .cub_wrapper:hover > .header__icon {
  fill: #e5e5e5;
  background-color: #e5e5e5;
}

.cube {
  width: 26px;
  height: 26px;
  transform-style: preserve-3d;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
  animation: spin 5s linear infinite;
  position: relative;
}

.side {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border: 1px solid #ff6b08;
  justify-content: center;
  align-items: center;
  padding: 1px;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
}

.front {
  transform: translateZ(12px);
}

.back {
  transform: rotateY(180deg)translateZ(12px);
}

.left {
  transform: rotateY(-90deg)translateZ(12px);
}

.right {
  transform: rotateY(90deg)translateZ(12px);
}

.top {
  transform: rotateX(90deg)translateZ(12px);
}

.bottom {
  transform: rotateX(-90deg)translateZ(12px);
}

@keyframes spin {
  0% {
    transform: rotateY(0deg);
  }

  100% {
    transform: rotateY(360deg);
  }
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
  }
}

.selector {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  width: 1px;
  border: 0;
  padding: 0;
  display: none;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  z-index: 100;
  background-color: rgba(0, 0, 0, .6);
  justify-content: center;
  align-items: center;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
  transform: scale(1);
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-hidden {
  transform: scale(1.1);
}

.grid-container {
  width: 100%;
  gap: 20px;
  padding: 20px 0;
  display: grid;
}

@media screen and (min-width: 768px) {
  .grid-container {
    grid-template-columns: 1fr 1fr;
    gap: 32px;
    padding: 60px 0;
  }
}

@media screen and (min-width: 1024px) {
  .grid-container {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 32px 16px;
    padding: 60px 0;
  }
}

.scroll-to-top {
  fill: #545454;
  cursor: pointer;
  visibility: visible;
  opacity: 1;
  background-color: rgba(0, 0, 0, 0);
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  bottom: 25px;
  right: 25px;
}

.scroll-to-top:hover, .dark .scroll-to-top {
  fill: #ff6b01;
}

.dark .scroll-to-top:hover {
  fill: #000;
}

.scroll-to-top--hidden {
  opacity: 0;
  visibility: hidden;
}

